@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');

/* #343541 */
.rubik {
  font-family: "Rubik", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}

*{
  font-family:"Rubik", sans-serif;
}

#root{
  margin:0;
}

body{
  margin:0;
  /* background:url("../media/student-dorms-brutalist-architecture-macedonia-skopje.jpg");
  background-repeat: no-repeat;
  background-size: contain; */
}

.imgBackground{
  position:fixed;
  object-fit:cover;
  width:100vw;
  height:100vh;
  z-index:-1;
}

.container{
  padding:80px 10px;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  height:100%;
  gap:10px;
}

.form{
  box-sizing: border-box;
  width:100%;
  max-width:500px;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  gap:10px;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
  padding:20px;
  border-radius:20px;
  background:linear-gradient(45deg, rgba(0, 255, 255, 0.5), rgba(255, 255, 255, 0.5));
  backdrop-filter:blur(10px);
}

.formDiv{
  width:100%;
  display:flex;
  flex-direction:column;
  gap:10px;
  justify-content:center;
}

.textInput{
  font-size:24px;
  box-sizing:border-box;
  border:none;
  border-radius:10px;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
  padding:10px;
  width:100%;
  max-width:100%;
}

#addTweetName{
  /* max-width:200px; */
}

#addTweetText{
  resize:none;
}

.submit{
  padding:10px 20px;
  border:none;
  box-shadow:0 0 10px;
  background-color:rgba(0, 128, 128, 0.8);
  font-size:24px;
  color:white;
  font-weight:bold;
  cursor:pointer;
  border-radius:10px;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
}

.submit:hover{
  background-color:rgba(0, 128, 128, 1);
  transition:all .4s linear;
}

.tweet{
  background:linear-gradient(45deg, aliceblue, white);
  padding:20px;
  border-radius:20px;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
  width:100%;
  box-sizing:border-box;
  display:flex;
  flex-direction:column;
  align-items:start;
  gap:10px;
}

.userName{
  margin:0;
}

.tweetText{
  margin:0;
}

.replyButton{
  font-size:18px;
  padding:5px 10px;
  background-color:rgba(0, 128, 128, 0.8);
  border:none;
  color:white;
  cursor:pointer;
  border-radius:10px;
  transition:all .4s linear;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.2);
}

.replyButton:hover{
  background-color:teal;
}

.replyCont{
  display:flex;
  gap:10px;
  width:100%;
}

.replyInput{
  width:100%;
  border-radius:10px;
  border:none;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
  padding:10px;
  font-size:18px;
}

.repliesContChild{
  background:teal;
  color:white;
  padding:20px;
  border-radius:20px 0 20px 20px;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
  max-width:450px;
  display:flex;
  flex-direction:column;
  align-items:start;
  gap:10px;
  position:relative;
}

.replyBubbleArrow{
  box-sizing:border-box;
  position:absolute;
  right:0;
  top:-8px;
  border-top:8px solid transparent;
  border-right:15px solid teal;
  z-index:0;
}

.z-1{
  z-index:1;
}

.tweetCont{
  display:flex;
  flex-direction:column;
  gap:10px;
  align-items:end;
  width:100%;
}

.repliesCont{
  display:flex;
  flex-direction:column;
  align-items:end;
  gap:10px;
}

.tweetsCont{
  max-width:500px;
  width:100%;
  display:flex;
  flex-direction:column;
  gap:10px;
  align-items:start;
}

.datePosted{
  color:teal;
  font-weight:normal;
}

.formH2{
  font-size:1.5em;
  font-weight:bold;
  text-align:center;
  margin:0;
  margin-bottom:10px;
  color:teal;
}

.header{
  margin:0;
  background:rgba(0, 128, 128, 0.5);
  backdrop-filter:blur(10px);
  padding:10px;
  position:fixed;
  z-index:1;
  box-sizing:border-box;
  width:100%;
}

.header h1{
  margin:0;
  color:white;
  text-align:center;
}

.invLoader{
  display:none !important;
}

.loaderParent{
  margin-left:auto;
  margin-right:auto;
}

@keyframes scroll{
  from {
    font-size:2em;
  }
  to {
    font-size:1.5em;
  }
}

@keyframes unScroll{
  from {
    font-size:1.5em;
  }
  to {
    font-size:2em;
  }
}

.unscrolled{
  font-size:2em;
  animation:unScroll .2s linear;
}

.scrolled{
  font-size:1.5em;
  animation:scroll .2s linear;
}

@media (max-width:441px){
  .container{
    padding:120px 10px;
  }
}

footer{
  /* background:linear-gradient(45deg, rgba(0, 255, 255, 0.3), rgba(255, 255, 255, 0.3)); */
  background:rgba(0, 128, 128, 0.5);
  padding:30px 10px;
  backdrop-filter:blur(10px);
}

footer h2{
  margin:0;
  color:white;
  text-align:center;
}

#waitDataLoader{
  padding-top:20px;
  margin-left:auto;
  margin-right:auto;
}

.badStr{
  color:red;
  border:1px solid red;
}

.invFooter{
  display:none;
}

.adminPost{
  border:2px solid coral;
  box-sizing:border-box;
}

.adminPost .userName{
  color:coral;
}

.rasporedBtn{
  color:white;
  background-color:rgba(0, 128, 128, 0.8);
  border:none;
  font-weight:bold;
  border-radius:999px;
  font-size:24px;
  padding:10px 30px;
  cursor:pointer;
  transition:all 0.2s linear;
  box-shadow:0 0 10px rgba(0,0,0,0.5);
}

.rasporedBtn:active{
  background-color:rgba(0, 128, 128, 1);
}

@keyframes tableShow {
  from {opacity:0;}
  to {opacity:1;}
}

.rasporedCont{
  max-width:500px;
  width:100%;
  overflow-x:auto;
  box-shadow:0 0 10px rgba(0,0,0,0.5);
  animation:tableShow .2s linear;
}

.rasporedTable{
  color:white;
  box-sizing:border-box;
  width:100%;
  background:rgba(0, 128, 128, 0.5);
  backdrop-filter:blur(15px);
}

.rasporedTable td, .rasporedTable th{
  padding:5px 10px;
  border:1px solid white;
}